section{
    height: 100vh;
    max-height: 1100px;
    position: relative;
    overflow: hidden;
}
.hero-wrapper{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    position: relative;
}
.slider-arrows{
    width: 50px;
    height: 50px;
    color: #fff;
    cursor: pointer;
    background: #000d1a;
    border-radius: 50px;
    padding: 10px;
    margin-right: 1rem;
    user-select: none;
    transition: 0.3s;
}
.slider-arrows:hover{
    background: #cd853f;
    transform: scale(1.05)
}
.slider-buttons{
    position: absolute;
    bottom: 50px;
    right: 50px;
    display: flex;
    z-index: 10;
}
.hero-slide{
    z-index: 1;
    width: 100%;
    height: 100%;
}
.hero-slider{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.hero-slider::before{
    content: '';
    position: absolute;
    z-index: 2;
    width: 100%;
    height: 100vh;
    bottom: 0vh;
    left: 0;
    overflow: hidden;
    opacity: 0.4;
    background: linear-gradient(
        0deg,
     rgba(0,0,0,0.2) 0%,
      rgba(0,0,0,0.2) 50% ,
       rgba(0,0,0,0.6) 100%
       );
}
.hero-image{
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    object-fit: cover;
}
.hero-content{
    position: relative;
    z-index: 10;
    display: flex;
    flex-direction: column;
    max-width: 1600px;
    width: calc(100% - 200px);
    color: #fff;
}
.house-title{
    font-size: clamp(1rem, 8vw, 2rem);
    font-weight: 400;
    text-transform: uppercase;
    text-shadow: 0px 0px 20px rgba(0,0,0,0.4);
    text-align: left;
    margin-bottom: 0.8rem;
}
.house-price{
    margin-bottom: 1.2rem;
    text-shadow: 0px 0px 20px rgba(0,0,0,0.4);
}
.arrow{
    margin-left: 0.5rem;
    vertical-align: middle;
}